const { ProfileService } = require('../../../services');

module.exports = async (req, res) => {
  try {
    const data = await ProfileService.findTrustNetworkProfileById(
      req.params.id,
      req.user._id,
    );
    res.success({
      ...data,
    });
  } catch (err) {
    res.error({
      message: 'api.user.get-profile.fail',
    });
  }
};
